.cart {
    color: white;
}

.cart_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60px;
    padding: 0 10px;
    height: 75%;
    transition: 0.1s;
}

.cart_container:hover {
    background-color: rgb(65, 65, 65);
    height: 100%;
}

.cart_num {
    background-color: white;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    cursor: default;
    -webkit-user-select: none;     
    -moz-user-select: none;
    -ms-user-select: none; 
    user-select: none; 
}